import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useSelector } from 'react-redux';
import ChatMessage from './ChatMessage';
import { generateChatResponse, generateSuggestedQuestion } from '../features/chat/chatService';

const ChatbotInterface = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState([]);
  const [inputText, setInputText] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [suggestedQuestions, setSuggestedQuestions] = useState([]);
  const messagesEndRef = useRef(null);
  
  const { user } = useSelector((state) => state.auth);
  const { footprints } = useSelector((state) => state.footprints);

  // Scroll to bottom of messages
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Generate suggested questions for the user
  const generateSuggestedQuestions = useCallback(async () => {
    try {
      const questions = [];
      
      // Generate 3 suggested questions
      for (let i = 0; i < 3; i++) {
        const question = await generateSuggestedQuestion(footprints);
        if (question && !questions.includes(question)) {
          questions.push(question);
        }
      }
      
      setSuggestedQuestions(questions.filter(Boolean));
    } catch (error) {
      console.error('Error generating suggested questions:', error);
    }
  }, [footprints]);

  // Initialize with welcome message when chat is opened
  useEffect(() => {
    if (isOpen && messages.length === 0) {
      const userName = user?.name ? user.name.split(' ')[0] : 'there';
      const welcomeMessage = {
        id: Date.now(),
        text: `Hi ${userName}! I'm your Carbon Coach. Ask me anything about your carbon footprint or how you can reduce your emissions.`,
        isUser: false,
        timestamp: new Date()
      };
      setMessages([welcomeMessage]);
      
      // Generate suggested questions based on user's footprint data
      if (footprints.length > 0) {
        generateSuggestedQuestions();
      }
    }
  }, [isOpen, messages.length, user, footprints.length, generateSuggestedQuestions]);

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;
    
    // Add user message
    const userMessage = {
      id: Date.now(),
      text: inputText,
      isUser: true,
      timestamp: new Date()
    };
    
    setMessages(prev => [...prev, userMessage]);
    setInputText('');
    setIsTyping(true);
    
    try {
      // Generate AI response
      const response = await generateChatResponse(inputText, footprints);
      
      // Add AI response with typing effect
      setTimeout(() => {
        const botMessage = {
          id: Date.now(),
          text: response,
          isUser: false,
          timestamp: new Date()
        };
        setMessages(prev => [...prev, botMessage]);
        setIsTyping(false);
        
        // Refresh suggested questions after conversation
        generateSuggestedQuestions();
      }, 1000);
    } catch (error) {
      console.error('Error generating response:', error);
      setIsTyping(false);
      
      // Add error message
      const errorMessage = {
        id: Date.now(),
        text: "I'm sorry, I couldn't generate a response. Please try again.",
        isUser: false,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    }
  };

  const handleQuestionClick = (question) => {
    setInputText(question);
    handleSendMessage();
  };

  const toggleChat = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="chatbot-container">
      {/* Chat toggle button */}
      <button 
        className="chat-toggle-button"
        onClick={toggleChat}
      >
        <i className="fas fa-comments"></i>
        Carbon Coach
      </button>
      
      {/* Chat interface */}
      {isOpen && (
        <div className="chat-interface">
          <div className="chat-header">
            <h3>Carbon Coach</h3>
            <button className="close-button" onClick={toggleChat}>
              <i className="fas fa-times"></i>
            </button>
          </div>
          
          <div className="chat-messages">
            {messages.map((message) => (
              <ChatMessage 
                key={message.id} 
                message={message.text} 
                isUser={message.isUser} 
              />
            ))}
            
            {isTyping && (
              <div className="typing-indicator">
                <span></span>
                <span></span>
                <span></span>
              </div>
            )}
            
            <div ref={messagesEndRef} />
          </div>
          
          {/* Suggested questions */}
          {suggestedQuestions.length > 0 && (
            <div className="suggested-questions">
              <p>You might want to ask:</p>
              <div className="questions-container">
                {suggestedQuestions.map((question, index) => (
                  <button 
                    key={index} 
                    className="question-button"
                    onClick={() => handleQuestionClick(question)}
                  >
                    {question}
                  </button>
                ))}
              </div>
            </div>
          )}
          
          {/* Input area */}
          <div className="chat-input-area">
            <input
              type="text"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              placeholder="Ask me about reducing your carbon footprint..."
              onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
            />
            <button 
              className="send-button"
              onClick={handleSendMessage}
              disabled={!inputText.trim()}
            >
              <i className="fas fa-paper-plane"></i>
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ChatbotInterface;